"use client";

import { useState } from "react";
import { userGamestore } from "@/app/store/userGamestore";

export default function GameInput() {
  const [value, setValue] = useState("");

  const gameState = userGamestore((s) => s.gameState);
  const isMyTurn = gameState?.game?.isMyTurn ?? false;
  const isAiThinking = gameState?.game?.isAiThinking ?? false;

  const disabled = !isMyTurn || isAiThinking;

  return (
    <div className="mt-8 flex gap-3">
      <input
        className="border border-comment/50 rounded-lg bg-line px-3 py-2 text-fg w-64"
        placeholder={
          disabled ? "Waiting for your turn..." : "Type your word..."
        }
        disabled={true} // always disabled in Phase 4
        value={value}
        onChange={(e) => setValue(e.target.value)}
      />

      <button
        className="bg-comment/50 text-fg px-4 py-2 rounded-lg cursor-not-allowed"
        disabled={true}
      >
        Submit
      </button>
    </div>
  );
}
